// Ð—Ð°Ñ‰Ð¸Ñ‚Ð°: ÐµÑÐ»Ð¸ GSAP Ð¸Ð»Ð¸ ScrollTrigger Ð½Ðµ Ð¿Ð¾Ð´ÐºÐ»ÑŽÑ‡ÐµÐ½Ñ‹ â€” Ð²Ñ‹Ñ…Ð¾Ð´Ð¸Ð¼
if (typeof gsap === 'undefined' || typeof ScrollTrigger === 'undefined') {
  console.warn('GSAP or ScrollTrigger not loaded');
} else {
  gsap.registerPlugin(ScrollTrigger);

  const sections = document.querySelectorAll('.animated');

  // Ð•ÑÐ»Ð¸ Ð½ÐµÑ‚ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ð´Ð»Ñ Ð°Ð½Ð¸Ð¼Ð°Ñ†Ð¸Ð¸ â€” Ð¿Ñ€Ð¾ÑÑ‚Ð¾ Ð½Ð¸Ñ‡ÐµÐ³Ð¾ Ð½Ðµ Ð´ÐµÐ»Ð°ÐµÐ¼
  if (!sections.length) {
    console.info('No .animated elements found');
  } else {
    sections.forEach((section, i) => {
      const fromX = i % 2 === 0 ? -225 : 225;

      gsap.fromTo(
        section,
        { x: fromX, opacity: 0 },
        {
          x: 0,
          opacity: 1,
          duration: 1.4,
          ease: 'power2.out',
          scrollTrigger: {
            trigger: section,
            start: 'top 90%',
            end: 'bottom 15%',
            toggleActions: 'play none none none',
          },
        }
      );
    });
  }
}
